
import React, { useEffect, useRef, useState } from 'react';
import { Eye, EyeOff } from 'lucide-react';
import { userAuthAPI } from '../api';
import { identifyUser, setTag } from '../services/analytics'

interface LoginProps {
  onLogin: () => void;
}

export const Login: React.FC<LoginProps> = ({ onLogin }) => {
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [showPassword, setShowPassword] = useState(false);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [viewportStyle, setViewportStyle] = useState<{ height: string | number; top: string | number }>({ height: '100%', top: 0 });
  const containerRef = useRef<HTMLDivElement>(null);
  const userInputRef = useRef<HTMLInputElement>(null);
  const passInputRef = useRef<HTMLInputElement>(null);

  useEffect(() => {
    const handleVisualViewport = () => {
      if (window.visualViewport) {
        setViewportStyle({
          height: `${window.visualViewport.height}px`,
          top: `${window.visualViewport.offsetTop}px`
        });
      }
    };

    if (window.visualViewport) {
      window.visualViewport.addEventListener('resize', handleVisualViewport);
      window.visualViewport.addEventListener('scroll', handleVisualViewport);
      handleVisualViewport();
    }

    return () => {
      if (window.visualViewport) {
        window.visualViewport.removeEventListener('resize', handleVisualViewport);
        window.visualViewport.removeEventListener('scroll', handleVisualViewport);
      }
    }
  }, [])

  useEffect(() => {
    try {
      const uid = localStorage.getItem('user_id') || '0'
      identifyUser({ userId: uid, pageId: 'LOGIN', name: '' })
      setTag('', '')
    } catch {}
  }, [])

  useEffect(() => {
    try { userInputRef.current?.focus(); } catch { }
  }, [])

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setError(null);
    if (!username.trim() || !password.trim()) { setError(''); return; }
    setLoading(true);
    try {
      const data = await userAuthAPI.login(username.trim(), password.trim());
      if (data?.access_token) try { localStorage.setItem('user_access_token', data.access_token) } catch { }
      if (data?.refresh_token) try { localStorage.setItem('user_refresh_token', data.refresh_token) } catch { }
      onLogin();
    } catch {
      setError('');
    } finally {
      setLoading(false);
    }
  };

  return (
    <div ref={containerRef} className="fixed left-0 w-full bg-primary-50 overflow-y-auto no-scrollbar scroll-smooth font-sans animate-in fade-in duration-700" style={{ ...viewportStyle, overscrollBehavior: 'none' }}>
      <div className="h-full w-full max-w-md mx-auto bg-white rounded-none md:rounded-3xl shadow-2xl border border-slate-200 overflow-hidden">
        <div className="min-h-full w-full flex flex-col px-8 py-10">
          <div className="w-full flex flex-col items-center z-10 max-w-sm m-auto">

          {/* Title Group */}
          <div className="text-center mb-12">
            <h1 className="text-[40px] font-extrabold text-[#A855F7] mb-0 tracking-tight font-sans">
              YumeCrush
            </h1>
          </div>

          {/* Form */}
          <form onSubmit={handleSubmit} className="w-full space-y-5">
            {/* Username/Email Input */}
            <input
              type="text"
              placeholder=""
              value={username}
              onChange={(e) => setUsername(e.target.value)}
              ref={userInputRef}
              className="w-full h-[60px] px-6 rounded-2xl bg-white border border-[#F3F4F6] text-[#374151] placeholder:text-[#D1D5DB] outline-none focus:border-[#C084FC] focus:bg focus:ring-4 focus:ring-[#F3E8FF] transition-all text-[15px] font-medium shadow-sm"
            />

            {/* Password */}
            <div className="relative">
              <input
                type={showPassword ? "text" : "password"}
                placeholder=""
                value={password}
                onChange={(e) => setPassword(e.target.value)}
                ref={passInputRef}
                className="w-full h-[60px] px-6 rounded-2xl bg border border-[#F3F4F6] text-[#374151] placeholder:text-[#D1D5DB] outline-none focus:border-[#C084FC] focus:bg focus:ring-4 focus:ring-[#F3E8FF] transition-all text-[15px] font-medium shadow-sm"
              />
              <button
                type="button"
                onClick={() => setShowPassword(!showPassword)}
                className="absolute right-5 top-1/2 -translate-y-1/2 text-[#9CA3AF] hover:text-[#C084FC] transition-colors"
              >
                {showPassword ? <EyeOff size={20} /> : <Eye size={20} />}
              </button>
            </div>

            {/* Login Button */}
            {error && (
              <div className="text-red-500 text-xs font-bold">{error}</div>
            )}
            <button
              type="submit"
              disabled={loading}
              className={`w-full h-[60px] ${loading ? 'bg-[#A855F7]/70' : 'bg-[#A855F7] hover:bg-[#9333EA]'} text-white font-bold text-[16px] rounded-2xl shadow-[0_10px_30px_rgba(168,85,247,0.3)] active:scale-[0.98] transition-all mt-4`}
            >
              {loading ? '...' : ''}
            </button>
          </form>
        </div>
      </div>
      </div>
    </div>
  );
};
